<template>
    <p-card style="width: 500px; margin: auto;">
        <template #title>
            Permissions
        </template>
        <template #content>
            <p-data-table :value="permissions">
                <p-column header="App" field="label"></p-column>

                <p-column header="Read" field="read" style="width: 60px">
                    <template #body="{data}">
                        <p-checkbox v-model="data.read" :binary="true" />
                    </template>
                </p-column>

                <p-column header="Create" field="create" style="width: 60px">
                    <template #body="{data}">
                        <p-checkbox v-model="data.create" :binary="true" />
                    </template>
                </p-column>

                <p-column header="Update" field="update" style="width: 60px">
                    <template #body="{data}">
                        <p-checkbox v-model="data.update" :binary="true" />
                    </template>
                </p-column>

                <p-column header="Delete" field="delete" style="width: 60px">
                    <template #body="{data}">
                        <p-checkbox v-model="data.delete" :binary="true" />
                    </template>
                </p-column>
            </p-data-table>
        </template>
        <template #footer>
            <p-button
                icon="pi pi-check"
                label="Save"
                @click="save"
                :loading="loading"
            />
        </template>
    </p-card>
</template>

<script>
import { computed, onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";
import { useConfirm } from "primevue/useconfirm";
export default {
    setup() {
        const store = useStore();
        const route = useRoute();
        const permissions = ref([]);
        const confirm = useConfirm();
        const router = useRouter();

        const permissionsKeys = computed(() => {
            return permissions.value.map((item) => item.appKey);
        });
        const appKeyOptions = computed(() => {
            return store.state.maintanance.appKeyList.filter((item) => {
                return !permissionsKeys.value.includes(item.value);
            });
        });

        const processPermissionsList = (items) => {
            const permissionsKeys = items.map((item) => item.appKey);
            const rest = store.state.maintanance.appKeyList
                .filter((item) => {
                    return !permissionsKeys.includes(item.value);
                })
                .map((item) => {
                    return {
                        ...item,
                        appKey: item.value,
                        create: false,
                        delete: false,
                        read: false,
                        update: false
                    };
                });
            permissions.value = [...items, ...rest];
        };

        const getUserGroupPermission = () => {
            store
                .dispatch(
                    "maintanance/getUserGroupPermissions",
                    route.params.id
                )
                .then((data) => {
                    processPermissionsList([...data.permissions]);
                })
                .catch(() => {});
        };

        const loading = ref(false);
        const save = () => {
            loading.value = true;
            store
                .dispatch("maintanance/saveUserGroupPermission", {
                    gId: route.params.id,
                    permissions: [...permissions.value]
                })
                .then((data) => {
                    confirm.require({
                        message: "You need to re-login changes to take effect",
                        header: "Re-login please",
                        icon: "pi pi-info",
                        accept: () => {
                            store.dispatch("logout").then(() => {
                                router.push({ name: "Login" });
                            });
                        }
                    });
                })
                .finally(() => {
                    loading.value = false;
                });
        };

        const getAppKeyList = () => {
            store
                .dispatch("maintanance/getAppKeyList")
                .then()
                .catch((err) => {
                    console.log(err);
                });
        };

        onMounted(() => {
            getAppKeyList();
            getUserGroupPermission();
        });

        return {
            permissions,
            save,
            loading,
            appKeyOptions
        };
    }
};
</script>
